import React from 'react';
import { HashRouter, Route } from 'react-router-dom';
import MachineInfo from './component/MachineInfo';
import { TicketInfoProvider } from './utils/TicketInfoProvider';
import AppBarMain from './component/AppBarMain';
import TicketRaise from './component/TicketRaise';
import LoginUser from './component/LoginUser';
import Home from './component/Home';
import Success from "./component/Success";

function App() {

  return (
    <div className="App">
      <AppBarMain />

      <TicketInfoProvider>

        <HashRouter>
          <Route exact path="/" component={Home} />
          <Route exact path="/machine-info" component={MachineInfo} />
          <Route exact path="/report-issue" component={TicketRaise} />
          <Route exact path="/user-auth" component={LoginUser} />
          <Route exact path="/success" component={Success} />
        </HashRouter>
      </TicketInfoProvider>

    </div>
  );
}

export default App;
