import React, { useState } from 'react';
import {
    Card, CardContent, makeStyles, Radio,
    RadioGroup, FormControlLabel, TextField, Typography
} from "@material-ui/core";

const useStyles = makeStyles({
    root: {
        minWidth: 275,
    }
});

export default function Issues({ onIssueSelect }) {
    var classes = useStyles();

    const [otherIssue, setOtherIssue] = useState("");

    return (
        <div>
            <Card className={classes.root} variant="outlined" >
                <CardContent>
                    <RadioGroup onChange={(e) => onIssueSelect(e.target.value)}  >
                        <FormControlLabel value="Machine is not responding." control={<Radio />} label={<Typography variant="h6">Machine is not responding.</Typography>} />
                        <FormControlLabel value="Machine is off." control={<Radio />} label={<Typography variant="h6">Machine is off.</Typography>} />
                        <FormControlLabel value="Cash struck in the machine." control={<Radio />} label={<Typography variant="h6">Cash struck in the machine.</Typography>} />
                        <FormControlLabel value="Token not dispensed after taking money." control={<Radio />} label={<Typography variant="h6">Token not dispensed after taking money.</Typography>} />
                        <FormControlLabel value={otherIssue} control={<Radio />} label={<TextField onChange={e => { setOtherIssue(e.target.value) }} label="Other" />} />
                    </RadioGroup>
                </CardContent>
            </Card>
        </div>
    )
}
