import React, { useState } from 'react';
import Issues from './Issues';
import { Button, makeStyles, Typography, Box, CircularProgress } from "@material-ui/core";
import db from "../utils/database";
import { TICKET_COLLECTION } from "../constants";
import { useTicketInfo } from "../utils/TicketInfoProvider";
import { Redirect } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
    }
}));



export default function TicketRaise() {
    var classes = useStyles();
    const [disable, setDisable] = useState(false);
    const [radioSeleted, setRadioSeleted] = useState(null);
    const [showError, setShowError] = useState(false);
    const [showLoading, setShowLoading] = useState(false);
    const [shouldRedirect, setShouldRedirect] = useState(false);
    const [newDoc, setNewDoc] = useState({})

    const globalTicketInfo = useTicketInfo();

    var submitHandler = () => {
        if (radioSeleted === null) {
            setShowError(true);
            return;
        }
        setShowLoading(true);
        setShowError(false);
        setDisable(true);

        var data = {
            report: radioSeleted,
            machineRefNo: globalTicketInfo.machineRefNo,
            datetime: new Date().getTime()
        };

        addNewTicket(data);



    };

    const addNewTicket = data => {
        db.collection(TICKET_COLLECTION)
            .add(data)
            .then(doc => {
                setShowLoading(false);
                setNewDoc(doc);
            })
            .catch(err => {
                setShowLoading(false);
            });

    };

    if (!globalTicketInfo.machineRefNo) {
        return (<Redirect to="/" />);
    }

    if(newDoc.id){
        return (<Redirect to={`/success?ref=${newDoc.id}`} />);
    }

    return (
        <>
            {!showLoading &&

                <Box m={2}>
                    <Typography variant="h5" gutterBottom>
                        Please choose or describe your Issue below.
                </Typography>

                    <Typography variant="subtitle2" gutterButton>
                        Machine Reference: {globalTicketInfo.machineRefNo}
                    </Typography>

                    <Issues onIssueSelect={setRadioSeleted} />
                    {showError && <Typography variant="subtitle2" gutterBottom color="error">Please select your issue.</Typography>}
                    <Button variant="contained" onClick={submitHandler} disabled={disable} color="primary" size="large" className={classes.margin}>Submit</Button>
                </Box>
            }
            {showLoading && <Box display="flex" justifyContent="center" m={1} p={1}>

                <CircularProgress color="secondary" />
            </Box>

            }
        </>
    )
}
