import React, { useState } from 'react';
import { AppBar, Toolbar, IconButton, makeStyles, Typography, Avatar, Box } from "@material-ui/core";
import { Menu as MenuIcon } from "@material-ui/icons";
import AppDrawer from './AppDrawer';
import { red } from "@material-ui/core/colors";
import logo from "../logo.png";


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    color: red[900]
  },
  title: {
    flexGrow: 1,
    color: red[900]
  },
  colorDefault: {
    background: "#ffffff",

  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  }
}));


export default function AppBarMain() {
  const classes = useStyles();
  const [openDrawer, setOpenDrawer] = useState(false);


  return (
    <div>

      <AppBar position="static" classes={{
        colorDefault: classes.colorDefault
      }} color="default">
        <Toolbar>
          <IconButton onClick={() => { setOpenDrawer(true) }} edge="start" className={classes.menuButton} aria-label="menu">
            <MenuIcon />
          </IconButton>
          <Box mr={1}>

            <Avatar src={logo} className={classes.small} />
          </Box>
          <Typography variant="h6" className={classes.title}>
            DMRC Fault Reporting System
                    </Typography>
          {/* <Button color="inherit">Login</Button> */}
        </Toolbar>
      </AppBar>
      <AppDrawer open={openDrawer} onCloseDrawer={setOpenDrawer} />


    </div>
  )
}
