import React from 'react';
import { Box, Paper, Typography, makeStyles } from "@material-ui/core";
import { green } from "@material-ui/core/colors";
import { parse } from "query-string";
import { useLocation, Redirect } from "react-router-dom";

function useQuery() {
    return parse(useLocation().search);

}

const useStyles = makeStyles({
    colorGreen :{
        color : green[900]
    }
});

export default function Success() {

    var { ref } = useQuery();
    const classes = useStyles();

    if (!ref) {
        return (<Redirect to="/" />);
    }


    return (
        <Box m={2} p={2} component={Paper} >
            <Typography variant="h6">Your query has been submitted to the DMRC grievance department. Your reference id is 
            <br/>
            <Typography variant="button" className={classes.colorGreen}>

                {ref}
            </Typography>
                
            </Typography>

        </Box>
    )
}
