import React, { useState, useEffect } from 'react';
import { Link, useLocation, Redirect } from 'react-router-dom';
import { parse } from "query-string";
import db from "../utils/database";
import { MACHINE_INFO_COLLECTION } from "../constants";
import { TableContainer, Paper, Table, TableBody, TableRow, TableCell, CircularProgress, Box, Typography, Button } from '@material-ui/core';

import { useTicketInfoUpdate } from "../utils/TicketInfoProvider";


function useQuery() {
    return parse(useLocation().search);

}

var getDate = timestamp =>{
    timestamp = timestamp*1000;
    var date = new Date(timestamp);
    return date.getDate() + '/' + (date.getMonth()+1) + '/' + date.getFullYear();
}



export default function MachineInfo() {

    const [machineInfo, setMachineInfo] = useState(undefined);
    const [firebaseError, setFirebaseError] = useState(null);
    const setGlobalTicketInfo = useTicketInfoUpdate();

    var { id } = useQuery();

    function getMachineInfoById(id) {
        db.collection(MACHINE_INFO_COLLECTION).doc(id)
            .get()
            .then(doc => {
                console.log(doc.data());
                if (doc.exists){
                    setMachineInfo({ ...doc.data(), });
                    setGlobalTicketInfo(doc.data());
                }else
                    setFirebaseError("Incorrect Machine QR Code.");

            })
            .catch(err => {
                console.log(err);
                setFirebaseError(err);
            });

    }

    useEffect(() => {

        if (id !== undefined && id !== null)
            getMachineInfoById(id);

    }, [id]);

    if (id === undefined || id === null) {
        return (<Redirect to="/" />);
    }



    return (

        <div>
            {machineInfo && <div>
                <Box p={2}>

                    <Typography variant="h6" >
                        Machine Information
                    </Typography>

                    <TableContainer component={Paper}>
                        <Table>
                            <TableBody>
                                <TableRow>
                                    <TableCell>Machine Reference Number</TableCell>
                                    <TableCell>{machineInfo.machineRefNo}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>Installation Date</TableCell>
                                    <TableCell>{getDate(machineInfo.installDate.seconds)}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>Installation Station</TableCell>
                                    <TableCell>{machineInfo.installStation}</TableCell>
                                </TableRow>

                            </TableBody>
                        </Table>
                    </TableContainer>
                    <Box mt={1} display="flex" justifyContent="center">

                        <Button variant="contained" color="secondary" to={`/report-issue?machineRef=${machineInfo.machineRefNo}`} component={Link}>Report Issue</Button>
                    </Box>
                </Box>

            </div>}
            {!machineInfo && !firebaseError &&

                <Box display="flex" justifyContent="center" m={1} p={1}>

                    <CircularProgress color="secondary" />
                </Box>
            }
            {firebaseError &&
                <Box m={2} display="flex" justifyContent="center">
                    <Paper>
                        <Box p={2}>
                            <Typography variant="h6" color="error" >
                                {firebaseError}
                            </Typography>
                        </Box>
                    </Paper>
                </Box>}
        </div>
    )
}
