import React, { useState, useContext } from "react";


const TicketInfoContext = React.createContext();
const TicketInfoUpdate = React.createContext();

export function useTicketInfo(){
    return useContext(TicketInfoContext);
}

export function useTicketInfoUpdate(){
    return useContext(TicketInfoUpdate);
}

export function TicketInfoProvider({ children }) {

    const [ticketInfo, setTicketInfo] = useState({});

    // function updateTicketInfo(data){

    //     setTicketInfo(data);

    // } 

    return (
        <TicketInfoContext.Provider value={ticketInfo}>
            <TicketInfoUpdate.Provider value={setTicketInfo}>
                {children}
            </TicketInfoUpdate.Provider>
        </TicketInfoContext.Provider>
    )
}